.about_us_preview{
    width: 100vw;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    color: #fff;
}


.offer_preview_button span a{
    text-decoration: none;
    color: black;
}

.about_us_prev_normal{
    background-image: url('../media/about_us_prev.jpg'); /* Добавляем фон */
    -webkit-box-shadow: inset 0 -0.3vw 10vw black;
            box-shadow: inset 0 -0.3vw 10vw black;
}

.about_us_prev_reverse{
    -webkit-box-shadow: inset 0 0.3vw 10vw black;
            box-shadow: inset 0 0.3vw 10vw black;
    background-image: url('../media/about_us_prev_r.jpg'); /* Добавляем фон */
}

.about_us_description_box{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;  
    -webkit-box-orient: vertical;  
    -webkit-box-direction: normal;  
        -ms-flex-direction: column;  
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}


.about_us_preview_box{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 80%;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.about_us_preview_box>div{
    width: 58%;
    /* margin */
}

