

@font-face {
  font-family: 'Raleway-Regular'; /* Имя шрифта */
  src: url(fonts/Raleway-Regular.ttf); /* Путь к файлу со шрифтом */
 }

@font-face{
  font-family: 'Seravek';
  src: url(fonts/Seravek.ttf);
}

@font-face{
  font-family: 'Raleway-SemiBold';
  src: url(fonts/Raleway-SemiBold.ttf);
}

.scroll_to_top{
  position: fixed;
  right: 20px;
  bottom: 50px;
  z-index: 10000;
  cursor: pointer;
}


.scroll_to_top img{
  width: 75px;
}


.App{
  overflow-x: hidden;
  width: 100vw;
}

 *{
   font-family: 'Raleway-Regular'
 }

 @media (min-width: 1200px){

 h2{
   font-size: 50px;
   font-weight: 300
 }

 h3{
  font-size: 30px;
  font-weight: 300;
  }

  h4{
    font-size: 28px;
    line-height: 25px;
    font-weight: 300;
  }

 p{
   font-size: 18px;
   line-height: 20px;
 }

 .company_color{
  color: #85B728
 }

 }

 @media screen and (min-width:960px) and (max-width: 1200px){

  h2{
    font-size: 50px;
    font-weight: 300
  }
 
  h3{
   font-size: 30px;
   font-weight: 300;
   line-height: 30px;
   }
 
   h4{
     font-size: 25px;
     line-height: 20px;
     font-weight: 300;
   }
 
  p{
    font-size: 18px;
    line-height: 20px;
  }
 
  .company_color{
   color: #85B728
  }

 }

