
.slider_container{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.slider_info_box{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.slider_picture_box{
    overflow-y: hidden;
    overflow-x: hidden;
}


.slider_picture_box {
    overflow-y: none; /* for horiz. scroll, otherwise overflow-y: scroll; */
    -ms-overflow-style: none;
    overflow: -moz-scrollbars-none;
    scrollbar-width: none;
}

@media screen and (min-width: 800px){
    .slider_picture_box{
        width: 180px;
        height: 180px;
    }
    .slider_button{
        position: absolute;
        top: 20%;
        position: absolute0;
        width: 2.5%;
        cursor: pointer;
    }
}

@media screen and (max-width: 800px){
    .slider_picture_box{
        width: 180px;
        height: 180px;
    }
    .slider_button{
        position: absolute;
        top: 25%;
        position: absolute0;
        width: 5%;
        cursor: pointer;
    }
}

@media screen and (max-width: 500px){
    .slider_picture_box{
        width: 130px;
        height: 130px;
    }
    .slider_button{
        position: absolute;
        top: 20%;
        position: absolute0;
        width: 4.5%;
        cursor: pointer;
    }
}

@media screen and (max-width: 420px){
    .slider_picture_box{
        width: 130px;
        height: 130px;
    }
    .slider_button{
        position: absolute;
        top: 25%;
        position: absolute0;
        width: 5%;
        cursor: pointer;
    }
}

.slider_picture_box{
    margin-top: 10px;
    border-radius: 50%;
    overflow: hidden;
    border: 5px solid white;
    box-shadow: 2px 2px 10px rgb(160, 160, 160);
    display: flex;
    justify-content: center;
    align-items: center;

}
.slider_picture_box img{
    width: 100%;
}

.slider_info_box>p{
    margin-top: 25px;
    padding-left: 10%;
    padding-right: 10%;
    text-align: center;
    white-space: pre-wrap;
}

.slider_prev_button{
    left: -4%;
}

.slider_next_button{
    right: -4%;
    transform: rotate(180deg);
}


.slider_button img{
    width: 100%;
}


